* {
  box-sizing: border-box;
}

html {
  font-family: "Open Sans Condensed", sans-serif;
  font-size: 15px;
}

body,
ul,
ol,
h1,
h2,
h3,
h4,
h5,
h6,
p {
  margin: 0;
  padding: 0;
}

ul,
ol {
  list-style: none;
  padding: 0;
}

input,
button {
  border: none;
}

body {
  // background-color: #fff;
  background-color: rgb(0, 30, 60);
}

#root {
  height: 100vh;
}