.app-header {
  overflow: hidden;
  background-color: #f1f1f1;
  padding: 20px 10px;
}

.app-header-list {
  &__link {
    float: left;
    color: black;
    text-align: center;
    padding: 12px;
    text-decoration: none;
    font-size: 18px; 
    line-height: 25px;
    border-radius: 4px;
  }
  &__link:hover {
    background-color: #ddd;
    color: black;
  }
}

.active {
  background-color: dodgerblue;
  color: white;
}



